import React from 'react';

import './App.css';
import { CoverBoard } from './conva/CoverBoard';
import { useApiContext } from 'contexts';
import { ApiInput } from 'uicomponents/apiInput';

function App() {
  const { apiKey } = useApiContext();

  if (apiKey.DiscogsKey && apiKey.DiscogsSecret) {
    return (
      <div className="App">
        <CoverBoard />
      </div>
    );
  }
  return (
    <div className="App">
      <ApiInput open />
    </div>
  );
}
export default App;
